function onStart(){
    // ÑÑ‚Ð²Ð¾Ñ€ÑŽÑ”Ð¼Ð¾ Ð¼Ð°ÑÐ¸Ð², Ñ‰Ð¾Ð± Ð¿ÐµÑ€ÐµÐ²Ñ–Ñ€Ð¸Ñ‚Ð¸ Ð½Ð° Ð²Ð¸Ð³Ñ€Ð°ÑˆÐ½Ð¸Ð¹ Ñ€ÐµÐ·ÑƒÐ»ÑŒÑ‚Ð°Ñ‚Ð°
    let counter = 0;

    [...container.children].forEach((box) => box.textContent = '');

    // Ð½ÐµÐ°ÐºÑ‚Ð¸Ð²Ð½Ð¸Ð¹ ÐµÐ»ÐµÐ¼ÐµÐ½Ñ‚ box Ð¼Ð¾Ð¶Ð½Ð° Ð·Ð°Ð¼Ñ–Ð½Ð¸Ñ‚Ð¸ Ð½Ð° _ -Ð½Ð¸Ð¶Ð½Ñ” Ð¿Ñ–Ð´ÐºÑ€ÐµÑÐ»ÐµÐ½Ð½Ñ- Ñ‰Ð¾Ð± Ð²Ñ–Ð½ Ð½Ðµ Ð·Ð°Ð²Ð°Ð¶Ð°Ð² Ð´Ð¸Ð²Ð¸Ñ‚Ð¸ÑÑ ÐºÐ¾Ð½ÑÐ¾Ð»ÑŒ
    const promises = [...container.children].map((_, i)=> createPromise(i));
    Promise.allSettled(promises).then((items) => {
        items
        .forEach((item, i) => {
            // console.log(item);
            setTimeout(() => {
                if (item.status === 'fulfilled') {
                    counter += 1;
                }
            container.children[i].textContent = item.value || item.reason;

           if (container.children.length - 1 === i) {
            setTimeout(() => {
                if (counter === container.children.length || !counter) {
                    alert('WINNER!')
                } else {
                    alert('LOST MONEY!!!')
                }
            }, 500);
         
           }
       
        }, i * 1000);
        });
   
    })
    
    
    console.log(promises);
}


